

@mixin principal-title {
    font-size: 22px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 7px;
}

@mixin secondary-title {
    color: #777;
    font-size: 11px;
    font-weight: 300;
    letter-spacing: 7px;
    text-transform: uppercase;
    margin-bottom: 21px;
}

