}
.footer .img {
  width: 6.25rem;
  height: 2.1875rem;
}
.footer .logo {
  letter-spacing: 0;
  font-weight: 600;
}
.footer svg {
  fill: white;
}

html {
  overflow-x: none;
  font-size: 100%;
}

header {
  height: 23.75rem;
  margin-bottom: 5.625rem;
}
header a {
  text-decoration: none;
}
header .bottomOne__logo {
  width: 12.5rem;
}
header .bottomOne__span {
