}
header .topOne .vr {
  margin-left: 1.875rem;
  margin-right: 1.875rem;
}

body .title {
  font-size: 22px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 7px;
}
body .secondary-title {
  color: #777;
  font-size: 11px;
  font-weight: 300;
  letter-spacing: 7px;
  text-transform: uppercase;
