.row {
  display: flex;
}

.col-1 {
  flex: 0 0 8.3333333333%;
}

.col-2 {
  flex: 0 0 16.6666666667%;
}

.col-3 {
  flex: 0 0 25%;
}

.col-4 {
  flex: 0 0 33.3333333333%;
}

.col-5 {
  flex: 0 0 41.6666666667%;
}

.col-6 {
  flex: 0 0 50%;
}

.col-7 {
  flex: 0 0 58.3333333333%;
}

.col-8 {
  flex: 0 0 66.6666666667%;
}

.col-9 {
  flex: 0 0 75%;
}

.col-10 {
  flex: 0 0 83.3333333333%;
}

.col-11 {
  flex: 0 0 91.6666666667%;
}

.col-12 {
  flex: 0 0 100%;
}

.circle {
  display: flex;
  font-size: 1.125rem;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  color: white;
  width: 1.875rem;
  height: 1.875rem;
  border-radius: 3.125rem;
  background-color: #4b4b4b;
  margin-right: 0.625rem;
}
.circle svg,
.circle img {
  fill: white;
  height: 0.9375rem;
  width: 0.9375rem;
}
.circle:hover {
  background-color: #0d0d0d;
}

.circle__card {
  display: flex;
  font-size: 1.125rem;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  color: white;
  width: 3.75rem;
  height: 3.75rem;
  border-radius: 3.125rem;
  background-color: black;
}
.circle__card svg,
.circle__card img {
  fill: white;
  height: 1.875rem;
  width: 1.875rem;
}
.circle__card:hover {
  background-color: #0d0d0d;
}

.circle__top__card {
  display: flex;
  font-size: 1.125rem;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  color: white;
  width: 5rem;
  height: 5rem;
  border-radius: 3.125rem;
  background-color: black;
  font-size: 0.8125rem;
  text-transform: uppercase;
}
.circle__top__card svg,
.circle__top__card img {
  fill: white;
  height: 1.875rem;
  width: 1.875rem;
}
.circle__top__card:hover {
  background-color: #0d0d0d;
}
.circle__top__card p {
  margin-top: auto;
  margin-bottom: auto;
  margin-right: 50%;
  margin-left: 50%;
  text-align: center;
}

.circle_white {
  display: flex;
  font-size: 1.125rem;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  color: black;
  width: 5rem;
  height: 5rem;
  border-radius: 3.125rem;
  background-color: white;
  margin-left: 0.75rem;
  margin-right: 0.75rem;
  z-index: 1;
  opacity: 0.85;
}
.circle_white svg,
.circle_white img {
  fill: black;
  height: 5rem;
  width: 5rem;
}
.circle_white:hover {
  background-color: white;
}

.circle_fade {
  display: flex;
  font-size: 1.125rem;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  color: black;
  width: 4.375rem;
  height: 4.375rem;
  border-radius: 3.125rem;
  background-color: #4b4b4b;
  margin-left: 20px;
  margin-right: 20px;
  z-index: 1;
  opacity: 0.5;
}
.circle_fade svg,
.circle_fade img {
  fill: black;
  height: 4.375rem;
  width: 4.375rem;
}
.circle_fade:hover {
  background-color: #585858;
}

.description {
  margin-bottom: 5.625rem;
}
.description__paragraph {
  margin-bottom: 3.125rem;
}

.form {
  margin-bottom: 1.25rem;
}
.form__img {
  width: 48.125rem;
}
.form__right {
  margin-left: 1.875rem;
}
.form__title {
  font-size: 22px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 7px;
}
.form__group {
  margin-bottom: 4.0625rem;
}
.form__group--title {
  text-transform: uppercase;
  font-size: 0.8125rem;
  font-weight: 700;
}
.form__group--smallTitle {
  margin-bottom: 0.75rem;
  font-size: 0.8125rem;
  text-transform: capitalize;
  color: #4b4b4b;
}

@media (min-width: 20em) {
  .form__img {
    margin-right: 30px;
    margin-left: 30px;
    height: 100%;
    max-width: 100%;
    margin: 0;
    padding: 0;
  }
  .form__right {
    margin-top: 3.125rem;
  }
}
@media (min-width: 65em) {
  .from__img {
    height: 26.25rem;
  }
}
.border-w90 {
  background-color: black;
  height: 0.125rem;
  width: 5.625rem;
