  margin-bottom: 1.25rem;
}

.arrow {
  border: solid white;
  border-width: 0 0.125rem 0.125rem 0;
  display: inline-block;
  padding: 0.125rem;
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
}

.card {
  height: auto;
  min-width: auto;
  border-radius: 0;
  border: 0;
}
.card .card-img {
  height: auto;
}
.card .card-img-overlay {
