  justify-content: start;
  align-items: center;
  padding-left: 1.5625rem;
  border: 1px solid #909090;
}
.card__footer-active {
  font-size: 22px;
  font-weight: 600;
}
.card__footer-inactive {
  margin-left: 30px;
  color: #4b4b4b;
  text-decoration: line-through;
  text-transform: uppercase;
}

.card-big {
  height: 29.375rem;
  min-width: 1.15625rem;
  border-radius: 0;
  border: 0;
}
.card-big .card-img {
  height: 29.375rem;
}
.card-big .card-img-overlay {
  margin-left: 11px;
  margin-right: 11px;
  height: 29.375rem;
  min-width: 1.15625rem;
}
.card-big__footer {
  height: 4.6875rem;
  display: flex;
  justify-content: start;
  align-items: center;
  padding-left: 1.5625rem;
  border: 1px solid #909090;
}
.card-big__footer-active {
  font-size: 22px;
  font-weight: 600;
}
.card-big__footer-inactive {
  margin-left: 30px;
  color: #4b4b4b;
  text-decoration: line-through;
  text-transform: uppercase;
}

.card-small {
  height: 15.625rem;
  min-width: 20%;
  border-radius: 0;
  border: 0;
}
.card-small .card-img {
  height: 15.625rem;
}
.card-small .card-img-overlay {
  margin-left: 11px;
  margin-right: 11px;
  height: 15.625rem;
  min-width: 20%;
}
.card-small__footer {
  height: 4.6875rem;
  display: flex;
  justify-content: start;
  align-items: center;
  padding-left: 1.5625rem;
  border: 1px solid #909090;
}
.card-small__footer-active {
  font-size: 22px;
  font-weight: 600;
}
.card-small__footer-inactive {
  margin-left: 30px;
  color: #4b4b4b;
  text-decoration: line-through;
  text-transform: uppercase;
}
.card-small .card-img-overlay {
  display: none;
}
.card-small .circle__card {
  opacity: 1;
}
.card-small:hover .circle__card {
  opacity: 1;
}
.card-small:hover .card-img-overlay {
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  background-color: rgba(191, 191, 191, 0.5);
}

